<template>
  <section>
    <v-sheet
      color="white"
      elevation="5"
      outlined
      rounded
      shaped
      class="boxauthoriation"
    >
      <v-row>
        <v-col class="text-center">
          <router-link to="/">
           <img src="@/assets/logo.png" />
          </router-link>
        </v-col>
        <v-col md="12">
          <h3 class="text-center titleform">Verify Your Account Now</h3>
          <v-form
            ref="form"
            v-model="valid"
            lazy-validation
            class="form-section"
          >
            <v-row>
              <v-col md="12" cols="12" v-if="email == ''">
                <v-text-field
                  v-model="email"
                  label="Email Address"
                  outlined
                  :rules="emailRules"
                  @keyup.enter.stop="validate"
                ></v-text-field>
              </v-col>
              <v-col md="12" cols="12">
                <v-text-field
                  v-model="otp"
                  label="OTP Code"
                          outlined
                  :rules="[rules.required]"
                ></v-text-field>
              </v-col>
              <v-col cols="12" class="submitbtn">
                <v-btn
                  :disabled="!valid"
                  class="submitbutton btn-dark"
                  @click="validate"
                  
                  v-if="!allLoadingAuth"
                >
                  Verify
                </v-btn>
                <v-progress-circular
                  v-if="allLoadingAuth"
                  indeterminate
                ></v-progress-circular>
                <div class="registerlink">
                  Don't have an account<router-link
                    to="/registration"
                    >Register</router-link
                  >
                </div>

                <v-progress-circular
                  v-if="allUserloading"
                  indeterminate
                ></v-progress-circular>
              </v-col>
            </v-row>
          </v-form>
        </v-col>
      </v-row>
    </v-sheet>
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Verify",
  data() {
    return {
      allUserloading: false,
      valid: true,
      email: "",
      otp: "",
      snackbar: false,
      showpassword: false,
      emailRules: [(v) => !!v || "Email is required"],
      rules: {
        required: (value) => !!value || "OTP Code is required.",
      },
    };
  },
  methods: {
    ...mapActions(["verify"]),
    validate() {
      if (this.$refs.form.validate()) {
        let userData = {
          email: this.email,
          otp: this.otp,
        };
        this.verify(userData);
        this.snackbar = true;
      }
    },
    reset() {
      this.$refs.form.reset();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    logout() {
      this.$store.dispatch("logoutUser");
    },
  },
  computed: {
    ...mapGetters(["allLoadingAuth"]),
  },
  created() {
    if (localStorage.getItem("emailAuth") != undefined) {
      this.email = localStorage.getItem("emailAuth");
    }
    if (localStorage.getItem("token") != undefined) {
      this.$router.push("/").catch((err) => {
        console.error(err);
      });
    }
  },
};
</script>

